:root {
  --basic-black: #000000;
  --basic-black-opacity-3: rgba(0, 0, 0, 0.3);
  --basic-black-opacity-6: rgba(0, 0, 0, 0.6);
  --basic-dust: #D7DCDE;
  --basic-gray: #EEEEEE;
  --basic-green: #00CA74;
  --basic-mist: #E1E1E1;
  --basic-red: #FB565A;
  --basic-yellow: #EFC849;
  --basic-white: #FFFFFF;
  --basic-white-opacity: rgba(255, 255, 255, 0.3);
  --basic-shadow: rgba(0, 1, 1, 0.1);
  --basic-transparent: transparent;
  --basic-peace: #A6A6A6;

  --special-black: #283136;
  --special-black-opacity: rgb(40, 49, 54, 0.3);
  --special-dust: #B4B9BB;
  --special-gray: #DFDFDF;
  --special-green: #00BC6C;
  --special-mist: #666666;
  --special-red: #E74246;
  --special-yellow: #EAB534;
  --special-peace: #231F20;

  --decorated-black: #444444;
  --decorated-gray: #D5D5D5;
  --decorated-green: #00AA62;
  --decorated-mist: #DBDBDB;
  --decorated-yellow: #E5A722;
  --decorated-red: #D7373B;

  /* body variables */

  --main-width: 1160px;

  /* action-button variables */

  --action-button-padding: 40px;
}

/* typography */

@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto.woff2") format("woff2"),
    url("../fonts/roboto.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/robotomedium.woff2") format("woff2"),
    url("../fonts/robotomedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/robotobold.woff2") format("woff2"),
    url("../fonts/robotobold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

/* tags */

body {
  margin: 0 auto;
  padding: 0;

  color: var(--basic-black);
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;

  background-color: var(--basic-white);
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  color: inherit;
  text-decoration: none;
}

/* common */

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.list-reset {
  margin: 0;
  padding: 0;

  list-style: none;
}

/* action-button */

.action-button {
  display: inline-block;
  padding: 17px var(--action-button-padding, 40px) 15px;

  color: var(--basic-white);
  font-weight: 500;
  text-align: center;
  text-transform: uppercase;

  cursor: pointer;
  background-color: var(--basic-red);
  border: none;
  border-radius: 3px;
}

.action-button:hover {
  background-color: var(--special-red);
}

.action-button:active,
.action-button:focus {
  color: var(--basic-white-opacity);

  background-color: var(--decorated-red);
  outline: none;
}

.action-button--green {
  background-color: var(--basic-green);
}

.action-button--green:hover {
  background-color: var(--special-green);
}

.action-button--green:active,
.action-button--green:focus {
  background-color: var(--decorated-green);
}

.action-button--yellow {
  background-color: var(--basic-yellow);
}

.action-button--yellow:hover {
  background-color: var(--special-yellow);
}

.action-button--yellow:active,
.action-button--yellow:focus {
  background-color: var(--decorated-yellow);
}

.action-button--gray {
  color: var(--basic-black);

  background-color: var(--basic-gray);
}

.action-button--gray:hover {
  background-color: var(--special-gray);
}

.action-button--gray:active,
.action-button--gray:focus {
  color: var(--basic-black-opacity-3);

  background-color: var(--decorated-gray);
  box-shadow: inset 0 3px 0 var(--basic-shadow);
}

/* main-header */

.main-header {
  background-color: var(--basic-gray);
}

.main-header__navigation {
  width: var(--main-width);
  margin: 0 auto;
}

.main-header__navigation a {
  display: inline-block;

  font-weight: 500;
  line-height: 30px;
  text-transform: uppercase;
}

.main-header__navigation a[href]:hover {
  color: var(--basic-red);
}

.main-header__navigation a[href]:active,
.main-header__navigation a[href]:focus {
  color: inherit;

  opacity: 0.3;
  outline: none;
}

.main-header__navigation a:not([href]):not(.main-header__logo)::after {
  content: "";

  display: block;
  width: 100%;
  height: 2px;

  background-color: var(--basic-red);
}

/* main */

.main__title-wrapper {
  background-color: var(--basic-gray);
}

.main__title {
  display: flex;
  justify-content: center;
  width: var(--main-width);
  margin: 0 auto;
  padding: 78px 0 108px;

  font-weight: 500;
  font-size: 55px;
  line-height: 55px;
}

/* principles */

.principles {
  width: var(--main-width);
  margin: 0 auto;
}

.principles__slide-title {
  font-weight: 500;
  font-size: 55px;
  line-height: 55px;
}

.principles__slide-description {
  color: var(--special-black);
}

.principles__slide-button {
  --action-button-padding: 54px;
}

/* services */

.services {
  width: var(--main-width);
  margin: 0 auto;
}

.services__service-title {
  font-weight: 700;
  text-transform: uppercase;
}

.services__service-description {
  color: var(--special-black);
}

/* about */

.about {
  width: var(--main-width);
  margin: 0 auto;
}

.about__title {
  font-weight: 500;
  font-size: 45px;
  line-height: 45px;
}

.about__description {
  color: var(--special-black);
}

.about__services-title {
  color: var(--special-black);
  font-weight: 700;
  text-transform: uppercase;
}

.about__services-list li {
  color: var(--special-black);
}

.about__properties-title {
  color: var(--special-black);
  font-weight: 700;
  text-transform: uppercase;
}

.about__properties-list dt {
  color: var(--special-black);
  line-height: 18px;
}

.about__properties-list dd {
  font-weight: 700;
  font-size: 45px;
  line-height: 64px;
}

.about__properties-list span {
  font-size: 26px;
  line-height: 40px;
  vertical-align: text-top;
}

/* partners */

.partners {
  width: var(--main-width);
  margin: 0 auto;
}

.partners__link img {
  opacity: 0.2;
}

.partners__link:hover img {
  opacity: 1;
}

.partners__link:active img,
.partners__link:focus img {
  opacity: 0.1;
}

/* placement */

.placement {
  position: relative;
}

.placement__description-wrapper {
  position: relative;
  z-index: 2;

  width: var(--main-width);
  margin: 0 auto;
  padding: 54px 0;
}

.placement__description {
  box-sizing: border-box;
  width: 319px;
  padding: 49px 50px 47px;

  background-color: var(--basic-white);
}

.placement__description-title {
  font-weight: 700;
  font-size: 18px;
  line-height: 30px;
  text-transform: uppercase;
}

.placement__description-address,
.placement__description-telephone {
  color: var(--special-mist);
  line-height: 18px;
}

.placement__description-button {
  --action-button-padding: 44px;
}

.placement__map {
  position: absolute;
  top: 0;
  z-index: 1;

  width: 100%;
}

.placement__map img {
  margin: 0 auto;
}

/* main-footer */


.main-footer {
  width: var(--main-width);
  margin: 0 auto;
}

.main-footer__link {
  display: flex;
  justify-content: center;
  width: 81px;
  height: 81px;

  background-color: var(--special-mist);
  border-radius: 50%;
}

.main-footer__link:hover {
  background-color: var(--special-red);
}

.main-footer__link:active,
.main-footer__link:focus {
  background-color: var(--decorated-red);
}

.main-footer__link:active img,
.main-footer__link:focus img {
  opacity: 0.3;
}

.main-footer__promotion-title {
  font-weight: 700;
  font-size: 36px;
  line-height: 36px;
}

.main-footer__promotion-description {
  color: var(--decorated-black);
  line-height: 22px;
}

/* popup */

.popup__title {
  font-weight: 700;
  font-size: 45px;
  line-height: 53px;
}

.popup__close-button {
  display: block;
  width: 21px;
  height: 21px;

  cursor: pointer;
  background-color: transparent;
  background-image: url("../img/close-cross.svg");
  border: none;
  opacity: 0.3;
}

.popup__close-button:hover {
  opacity: 1;
}

.popup__close-button:active,
.popup__close-button:focus {
  opacity: 0.1;
  outline: none;
}

.popup__field label {
  font-weight: bold;
  line-height: 18px;
}

.popup__field input,
.popup__field textarea {
  padding: 16px;

  line-height: 18px;

  background-color: var(--basic-white);
  border: 2px solid var(--basic-dust);
  border-radius: 3px;
}

.popup__field textarea {
  resize: none;
}

.popup__field input:hover,
.popup__field textarea:hover {
  border-color: var(--special-dust);
}

.popup__field input:invalid,
.popup__field textarea:invalid {
  border-color: var(--special-red);
}

.popup__field input::placeholder,
.popup__field textarea::placeholder {
  color: var(--decorated-black);

  opacity: 0.5;
}

.popup__field input:focus::placeholder,
.popup__field textarea:focus::placeholder {
  opacity: 1;
}

.popup__field input:invalid::placeholder,
.popup__field textarea:invalid::placeholder {
  color: var(--special-red);

  opacity: 1;
}

.popup__submit {
  --action-button-padding: 83px;
}

/* templates */

.templates {
  width: var(--main-width);
  margin: 0 auto;
}

.templates fieldset {
  border: none;
}

.templates legend {
  font-weight: 700;
  font-size: 18px;
  text-transform: uppercase;
}

.templates__cost-range-wrapper {
  width: 260px;
  height: 80px;

  background: var(--basic-gray);
  border-radius: 3px;
}

.templates__cost-range {
  display: block;
  margin: 0;
}

.templates__field-cost label {
  color: var(--special-black);
  line-height: 22px;
  text-transform: uppercase;
}

.templates__field-cost input {
  width: 80px;
  height: 38px;

  color: var(--special-black);
  line-height: 22px;
  text-align: center;

  background: var(--basic-gray);
  border: none;
  border-radius: 3px;
}

.templates__field-grid label,
.templates__field-features label {
  color: var(--special-black);
  line-height: 20px;
}

/* Изменяет цвет псевдоэлемента */
/* .templates__field-grid label:hover,
.templates__field-features label:hover {
} */

.templates__field-grid input:active + label,
.templates__field-grid input:focus + label,
.templates__field-features input:active + label,
.templates__field-features input:focus + label {
  color: var(--special-black-opacity);
}

/* Определяет состояние disabled */
/* .templates__field-grid input:disabled,
.templates__field-features input:disabled {
} */

.templates__filter-submit {
  --action-button-padding: 88px;
}

.templates__field-sort {
  color: var(--basic-black-opacity-3);
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
}

.templates__field-sort input:hover + label {
  color: var(--basic-black-opacity-6);
}

.templates__field-sort input:active + label,
.templates__field-sort input:focus + label {
  color: var(--basic-black);
}

.templates__field-sort-direction label {
  display: block;
  width: 0;
  height: 0;

  cursor: pointer;
  border: 5px solid var(--basic-transparent);
  border-top: 10px solid var(--basic-peace);
  opacity: 0.2;
}

.templates__field-sort-direction label:hover {
  opacity: 1;
}

.templates__field-sort-direction input:active + label,
.templates__field-sort-direction input:focus + label {
  border-top-color: var(--special-peace);
  opacity: 1;
}

.templates__field-sort-direction--ascending label {
  border-top-style: hidden;
  border-bottom: 10px solid var(--basic-peace);
}

.templates__field-sort-direction--ascending input:active + label,
.templates__field-sort-direction--ascending input:focus + label {
  border-top-style: hidden;
  border-bottom-color: var(--special-peace);
  opacity: 1;
}

.templates__field-sort input:disabled + label,
.templates__field-sort-direction input:disabled + label {
  display: none;
}

.templates__item-info {
  background-color: var(--basic-gray);
}

.templates__item-title {
  font-weight: 700;
  font-size: 18px;
  text-transform: uppercase;
}

.templates__item-title:hover {
  color: var(--basic-red);
}

.templates__item-title:active,
.templates__item-title:focus {
  color: var(--basic-black-opacity-3);

  outline: none;
}

.templates__item-description {
  color: var(--special-mist);
  line-height: 18px;
}

.templates__item-button {
  --action-button-padding: 72px;
}

.templates__pagination {
  --side-padding: 20px;
}

.templates__page-button {
  --action-button-padding: var(--side-padding);
}

.templates__page-button--next {
  --action-button-padding: 78px;
}

.templates__page-button:not([href]) {
  --width-border: 3px;

  padding-left: calc(var(--side-padding) - var(--width-border));
  padding-right: calc(var(--side-padding) - var(--width-border));

  cursor: default;
  background-color: var(--basic-white);
  border: var(--width-border) solid var(--decorated-mist);
}

.templates__page-button:not([href]):hover,
.templates__page-button:not([href]):active {
  color: inherit;

  box-shadow: none;
}
